import React from "react";
import css from './Pagination.module.css';

export const Pagination = (
    {
        page,
        pageSize,
        totalCount,
        goToPage,
    }
) => {
    const pagesCount = Math.ceil(totalCount / pageSize);
    // TODO Добавить поля ввода для pageSize и currentPage
    const pages = preparePages(page, pagesCount);
    const showLeftEllipsis = pages[0] > 1;
    const showRightEllipsis = pages[pages.length - 1] < totalCount;

    const pageList = pages.map(p => {
        return (
            <li key={p} className={"page-item " + (p === page ? " active disabled" : '')}>
                <span className={"page-link " + css.link} onClick={() => goToPage(p)}>
                    {p}
                </span>
            </li>
        )
    })

    return (
        <ul className="pagination pagination-sm">
            <li key={'first'} className="page-item disabled">
                {/* TODO method toFirstPage() */}
                <div className="page-link" onClick={() => goToPage(0)}>
                    &laquo;
                </div>
            </li>
            {showLeftEllipsis &&
            <li key={'left'} className="page-item disabled">
                <div className="page-link">
                    ...
                </div>
            </li>
            }
            {pageList}
            {showRightEllipsis &&
            <li key={'right'} className="page-item disabled">
                <div className="page-link">
                    ...
                </div>
            </li>
            }
            <li key={'last'} className="page-item disabled">
                {/* TODO method toLastPage() */}
                <div className="page-link" onClick={() => goToPage(-1)}>
                    &raquo;
                </div>
            </li>
        </ul>
    )
}

const preparePages = (current, count, neighbors = 1) => {
    let from, to;
    if (current - neighbors <= 1) {
        from = 1;
        to = Math.min(2 * neighbors + 1, count);
    } else if (current + neighbors >= count) {
        to = count;
        from = Math.max(1, count - 2 * neighbors);
    } else {
        from = current - neighbors;
        to = current + neighbors;
    }

    const pages = [];
    for (let i = from; i <= to; i++) {
        pages.push(i);
    }

    return pages;
};
