import React from "react";
import css from "./Users.module.css";
import noUserPhoto from "../../assets/images/no_small_photo.jpg";
import {NavLink} from "react-router-dom";

export const UserItem = (
    {
        user,
        toggleFollow,
        followingInProgress,
    }
    ) => {
    return (
        <div className={css.user}>
            <table>
                <tbody>
                <tr>
                    <td>
                        <div className={css.ava}>
                            <NavLink to={'/profile/' + user.id}>
                                {/*<img src={!!user.photos ? user.photos.small : noUserPhoto}*/}
                                <img src={noUserPhoto}
                                     className={css.ava} alt='alt'/>
                            </NavLink>
                        </div>
                    </td>
                    <td>
                        <div className={css.userName}>{user.username}</div>
                        <div>{user.role}</div>
                    </td>
                </tr>
                <tr>
                    <td colSpan={2}>
                        <div>
                            {
                                // TODO refactor
                                user.followed
                                    ?
                                    <button className="btn btn-secondary btn-sm"
                                            disabled={followingInProgress.some(id => id === user.id)}
                                            onClick={() => toggleFollow(user.id, false)}>Unfollow</button>
                                    :
                                    <button className="btn btn-success btn-sm"
                                            disabled={followingInProgress.some(id => id === user.id)}
                                            onClick={() => toggleFollow(user.id, true)}>Follow</button>
                            }
                        </div>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
    )
}
